// Authentication.js
import React, { useState } from 'react';
import { getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword } from 'firebase/auth';
import { getDatabase, ref, set } from 'firebase/database';
import { initializeApp } from 'firebase/app'; // Import initializeApp from firebase/app
// Import your Firebase config object from firebaseSetup.js
import { firebaseConfig } from './firebaseSetup'; // Replace with the actual path


initializeApp(firebaseConfig);

const auth = getAuth();
const database = getDatabase();

function Authentication({ setUsername, setIsRegistered, setIsEmailSet, registerEmail, setRegisterEmail, registerPassword, setRegisterPassword, username }) {
    const [isRegistering, setIsRegistering] = useState(true); // State to track whether the user is registering or logging in

    const handleRegister = async () => {
        if (username && registerEmail && registerPassword) {
            try {
                const userCredential = await createUserWithEmailAndPassword(auth, registerEmail, registerPassword);
                const user = userCredential.user;
                set(ref(database, 'users/' + user.uid), {
                    username: username,
                    email: registerEmail
                });
                setIsRegistered(true);
                setIsEmailSet(true);
            } catch (error) {
                console.error("Error registering user:", error);
            }
        } else {
            console.error("Please fill in all registration fields.");
        }
    };

    const handleLogin = async () => {
        if (registerEmail && registerPassword) {
            try {
                const userCredential = await signInWithEmailAndPassword(auth, registerEmail, registerPassword);
                const user = userCredential.user;
                // You can set any necessary state for logged-in users here
            } catch (error) {
                console.error("Error logging in:", error);
            }
        } else {
            console.error("Please fill in all login fields.");
        }
    };

    return (
        <div>
            <div>
                <button onClick={() => setIsRegistering(true)}>Register</button>
                <button onClick={() => setIsRegistering(false)}>Login</button>
            </div>
            {isRegistering ? (
                <div>
                    <input 
                        type="text"
                        placeholder="Enter Email for Registration"
                        value={registerEmail}
                        onChange={(e) => setRegisterEmail(e.target.value)}
                    />
                    <input 
                        type="password"
                        placeholder="Enter Password for Registration"
                        value={registerPassword}
                        onChange={(e) => setRegisterPassword(e.target.value)}
                    />
                    <input 
                        type="text" 
                        placeholder="Choose a username" 
                        value={username} 
                        onChange={(e) => setUsername(e.target.value)} 
                    />
                    <button onClick={handleRegister}>Register</button>
                </div>
            ) : (
                <div>
                    <input 
                        type="text"
                        placeholder="Enter Email for Login"
                        value={registerEmail}
                        onChange={(e) => setRegisterEmail(e.target.value)}
                    />
                    <input 
                        type="password"
                        placeholder="Enter Password for Login"
                        value={registerPassword}
                        onChange={(e) => setRegisterPassword(e.target.value)}
                    />
                    <button onClick={handleLogin}>Login</button>
                </div>
            )}
        </div>
    );
}

export default Authentication;
