import React, { useState, useEffect } from 'react';
import Cookies from 'js-cookie';
import Lottie from 'lottie-react'; // Import Lottie from 'lottie-react'
import LottieAnimation from './LottieLoader';
import loadingAnimation from './Lottie_Lego.json';
import { database, auth } from './firebaseSetup';
import Authentication from './Authentication';
import Chatbox from './Chatbox';
import MessageList from './MessageList';




function App() {
  const [username, setUsername] = useState('');
  const [hasProfile, setHasProfile] = useState(false);
  const [message, setMessage] = useState('');
  const [messages, setMessages] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isRegistered, setIsRegistered] = useState(false);
  const [isEmailSet, setIsEmailSet] = useState(false);

  useEffect(() => {
    const savedUsername = Cookies.get('username');
    if (savedUsername) {
      setUsername(savedUsername);
      setHasProfile(true);
    }
  
     // Access ref and onValue from the database object
  const messagesRef = database.ref('messages');
  messagesRef.on('value', (snapshot) => {
    const newMessages = [];
    snapshot.forEach((child) => {
      newMessages.push(child.val());
    });
    setMessages(newMessages);
    setIsLoading(false);
  });
}, []); // No need to include 'database' in the dependencies array


  return (
    <div className="App">
       <Lottie animationData={loadingAnimation} loop autoplay />
      {isEmailSet && isRegistered ? (
        <div>
          <h2>Welcome, {username}!</h2>
          <MessageList messages={messages} />
          <Chatbox 
            message={message} 
            setMessage={setMessage} 
            username={username} 
            database={database} 
          />
        </div>
      ) : (
        <Authentication 
          database={database} 
          auth={auth} 
          setUsername={setUsername} 
          setIsRegistered={setIsRegistered}
          setIsEmailSet={setIsEmailSet}
        />
      )}
    </div>
  );
}

export default App;
